import axios from 'axios';

const API_URL 	= 'https://manaenckov.design/api/notes/';

axios.defaults.headers.common = {
    Accept: "application/json, text/plain, */*"
};

export default class API {

    async send(method = 'GET', action, data = {}) {
        const response = await axios({
            method,
            url: `${API_URL}${action}`,
            data
        }).catch(error => {
            console.error('Error API:', error);
        });
        if(response && response.data){
            return response.data;
        } else return [];
    }

    async AddNote(data) {
        let response = await this.send('POST', 'add', data);

        console.log('API: ', 'AddNote', response);

        return response;
    }
    async Done(data) {
        let response = await this.send('PUT', `done`, data);

        console.log('API: ', 'Done', response);

        return response;
    }
    async Change(data) {
        let response = await this.send('PUT', `change`, data);

        console.log('API: ', 'Change', response);

        return response;
    }
    async Delete(data) {
        let response = await this.send('DELETE', `delete`, data);

        console.log('API: ', 'Delete', response);

        return response;
    }
    async GetUserNotes() {
        let response = await this.send('GET',  `get`, null);

        console.log('API: ', 'GetUserNotes', response);

        return response;
    }
    async GetExpUserNotes() {
        let response = await this.send('GET',  `getExp`, null);

        console.log('API: ', 'GetExpUserNotes', response);

        return response;
    }
}
