import React, { Component } from 'react';
import {
	PanelHeaderButton, Panel, PanelHeader, FormLayout, Input, Textarea, Button
} from '@vkontakte/vkui';

import Icon28DoneOutline from '@vkontakte/icons/dist/28/done_outline';
import Icon28CancelOutline from '@vkontakte/icons/dist/28/cancel_outline';
// `${new Date().getFullYear()}-${(new Date().getMonth()) < 10 ? `0${new Date().getMonth() + 1}` : new Date().getMonth() + 1}-${new Date().getDate()}`

import './Persik.css';

class Add extends Component {
	constructor(props) {
		super(props);

		this.state = {
			title: '',
			desk: '',
			time: '00:00',
			date: '',
			error: false
		};

	};

	 onChange = e => {

	 	const { name, value } = e.currentTarget;

	 	this.setState({
			[name]: value
		});
	};

	 send = () => {
	 	const { addTask, setPState, openErrorSnackbar } = this.props;

	 	if(!this.state.title) {
	 		this.setState({ error: true });
	 		setTimeout(() => 	this.setState({ error: false }), 2000)
			openErrorSnackbar('Заполните обязательные поля.');
	 		return;
		} else if (this.state.title.length > 500) {
			this.setState({ error: true });
			setTimeout(() => 	this.setState({ error: false }), 2000)
			openErrorSnackbar('Заголовок слишком длинный.');
			return;
		} else if (this.state.desk.length > 1000) {
			this.setState({ error: true });
			setTimeout(() => 	this.setState({ error: false }), 2000)
			openErrorSnackbar('Описание слишком длинное.');
			return;
		}/* else if (Date(new Date(this.state.date)).parse() < Date(new Date()).parse) {
			this.setState({ error: true });
			setTimeout(() => 	this.setState({ error: false }), 2000)
			openErrorSnackbar('Дата дедлайна не может быть в прошлом.');
			return;
		}*/
		 console.log(Date.parse(this.state.time), Date(new Date()).parse)
		 addTask({
			 title: this.state.title,
			 desk: this.state.desk,
			 time: this.state.date ? `${this.state.date}-${this.state.time}` : ''
		 });
		 setPState({ activeView: 'home' });
		 this.setState({
			 title: '',
			 desk: '',
			 time: '00:00'
		 })
	 };

	render() {

		const props = this.props;
		const { onChange, send } = this;

		return (
		<Panel id={props.id}>
			<PanelHeader
				left={
					<PanelHeaderButton
						onClick={() => props.setPState({
							activeView: 'home'
						})}
						><Icon28CancelOutline/>
					</PanelHeaderButton>
				}
			> Добавить</PanelHeader>
			<FormLayout>
				<Input
					onChange={onChange}
					name='title'
					top='Что необходимо сделать? (кратко)'
					placeholder='Покормить кота'
					maxLength='500'
					status={this.state.error &&  (!this.state.title || this.state.title.length > 500)? 'error' : 'default'}
                    value={this.state.title}
				/>
				<Textarea
					onChange={onChange}
					name='desk'
					top='Подробности задачи, если необходимо'
					status={this.state.error && this.state.desk.length > 1000 ? 'error' : 'default'}
					placeholder='Консервы лежат в ящике у холодильника.'
					maxLength='1000'
                    value={this.state.desk}
				/>
				<Input
					onChange={onChange}
					name='date'
					type='date'
					min={`${new Date().getFullYear()}-${(new Date().getMonth()) < 10 ? `0${new Date().getMonth() + 1}` : new Date().getMonth() + 1}-${new Date().getDate()}`}
					top='Укажите крайний срок выполнения, по желанию'
				/>
				<Input
					onChange={onChange}
					name='time'
					type='time'
					top='Укажите время к полю выше, по желанию'
					value={this.state.time}
				/>
				<Button
					mode='primary'
					size='xl'
					before={<Icon28DoneOutline/>}
					onClick={() => {
						send()
					}}
				>Создать задачу</Button>
			</FormLayout>
			{props.snackbar}
		</Panel>
	);
	};
};


export default Add;
