import React, { Component } from 'react';
import bridge from '@vkontakte/vk-bridge';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Add from './panels/Add';
import Change from './panels/Change';

import {
	Root, View, ScreenSpinner, Group, Alert,
	ModalPage, ModalRoot, ModalPageHeader, IS_PLATFORM_ANDROID, PanelHeaderButton, Header,
	Div, List, Cell, InfoRow, Button, Snackbar, Avatar, ConfigProvider
} from "@vkontakte/vkui";

import moment from 'moment'

import Icon24Dismiss from '@vkontakte/icons/dist/24/dismiss';
import Icon24Cancel from '@vkontakte/icons/dist/24/cancel';
import Icon24Delete from '@vkontakte/icons/dist/24/delete';
import Icon24Write from '@vkontakte/icons/dist/24/write';


import Icon16Clear from '@vkontakte/icons/dist/16/clear';
import Icon16Done from '@vkontakte/icons/dist/16/done';

import API from './API.js';

require('moment/locale/ru');

class App extends Component {

	constructor(props) {
		super(props);

		this.state = {
			tab: 'active',
			fetchedUser: {
				id: 12
			},
			popout: null,
			modal: null,
			snackbar: null,
			activeView: 'home',
			scheme: true ? 'space_gray' : 'bright_light',
			tasks: window.location.port ?  [{
				title: 'test',
				id: 1
			}] : false,
			expTasks: false,
			curTask: false
		};
	this.api = new API();
	};

	componentDidMount () {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'bright_light';
				document.body.attributes.setNamedItem(schemeAttribute);
				this.setState({
					scheme: data.scheme
				})
			}
			if (type === 'VKWebAppGetUserInfoResult') {
				this.setState({ fetchedUser: data });
				this.getTasks();
			}
		});
		bridge.send("VKWebAppGetUserInfo", {});
	};

	addTask = task => {
	 	const tasks = this.state.tasks;

		this.api.AddNote({
			id: this.state.fetchedUser.id,
			title: task.title,
			desk: task.desk ? task.desk : '',
			time: task.time ? task.time : '',
		}).then(res => {
			if(res === 'success' ){
				tasks.push({
					title: task.title,
					desk: task.desk ? task.desk : '',
					time: task.time ? task.time : '',
					done: false
				});
				this.setState({ tasks: tasks });
				if(this.state.tab === 'active') {
					this.getTasks();
				} else {
					this.getExpTasks();
				}
				console.log('good')
			} else {
				this.openErrorSnackbar('Произошла ошибка. #1');
				this.api.AddNote({
					id: this.state.fetchedUser.id,
					title: task.title,
					desk: task.desk ? task.desk : '',
					time: task.time ? task.time : '',
				});
			}
		}).catch(() => {
			this.openErrorSnackbar('Произошла ошибка. #2');
			this.api.AddNote({
				id: this.state.fetchedUser.id,
				title: task.title,
				desk: task.desk ? task.desk : '',
				time: task.time ? task.time : '',
			});
		})
	};
	 onCloseModal = e => {
		this.setState({ modal: null });
	};

	 getTasks = () => {
	 	this.setState({
			tasks: false,
			expTasks: false,
			tab: 'active'
		});
		 this.api.GetUserNotes(this.state.fetchedUser.id).then(tasks => {
				this.setState({ tasks });
		 }).catch(() => {
			 this.openErrorSnackbar('Произошла ошибка загрузки. #4');
			 this.setState({ tasks: [] });
		 });
	 };
	getExpTasks = () => {
		this.setState({
			expTasks: false,
			tasks: false,
			tab: 'expires'
		});
		this.api.GetExpUserNotes(this.state.fetchedUser.id).then(expTasks => {
				this.setState({ expTasks });
		}).catch(() => {
			this.openErrorSnackbar('Произошла ошибка загрузки. #5');
			this.setState({ expTasks: [] });
		});
	};

	openAlert = (key, e) => {
		this.setState({ popout:
				<Alert
					actionsLayout="vertical"
					actions={[{
						title: 'Удалить',
						autoclose: true,
						mode: 'destructive',
						action: () => this.delTask(key, e),
					}, {
						title: 'Отмена',
						autoclose: true,
						mode: 'cancel'
					}]}
					onClose={() => this.setState({
						popout: null
					})}
				>
					<h2>Подтвердите действие</h2>
					<p>Вы уверены, что хотите удалить задачу? <br/> Действие нельзя отменить.</p>
				</Alert>
		});
	};

	openDoneSnackbar = e => {
		this.setState({ snackbar:
				<Snackbar
					duration={2000}
					layout="vertical"
					onClose={() => this.setState({ snackbar: null })}
					before={<Avatar size={24} style={{ backgroundColor: '#4bb34b' }}><Icon16Done fill="#fff" width={14} height={14} /></Avatar>}
				>
					{e}
				</Snackbar>
		});
	};

	openErrorSnackbar = e => {
		this.setState({ snackbar:
				<Snackbar
					duration={2000}
					layout="vertical"
					onClose={() => this.setState({ snackbar: null })}
					before={<Avatar size={24} style={{ backgroundColor: '#FF0000' }}><Icon16Clear fill="#fff" width={14} height={14} /></Avatar>}
				>
					{e}
				</Snackbar>
		});
	};

	delTask = (key,e) => {
		let tasks = this.state.tasks || this.state.expTasks;
		this.setState({ popout: <ScreenSpinner/> });
		this.api.Delete({ note: e }).then(res => {
			this.setState({
				popout: null,
				modal: null
			});
			if(res !== 'success'){
				this.openErrorSnackbar('Произошла ошибка. #7');
				return;
			}
			tasks[key].done = tasks[key].done === 1 ? 0 : 1;

			tasks = tasks.filter(el => el.id !== e);

			if(this.state.tab === 'active') {
				this.setState({ tasks });
			} else {
				this.setState({ expTasks: tasks });
			}
			/*
			if(this.state.tab === 'active') {
				this.getTasks();
			} else {
				this.getExpTasks();
			}*/
		})

	};

	check = (key, id) => {
		let tasks = this.state.tasks || this.state.expTasks;
		this.api.Done({
			done: tasks[key].done === 1 ? 0 : 1,
			note: tasks[key].id
		}).then((res) => {

			if(res !== 'success'){
				this.openErrorSnackbar('Произошла ошибка. #6');
				return;
			}
			tasks[key].done = tasks[key].done === 1 ? 0 : 1;

			tasks = tasks.filter(e => e.id !== id);

			if(this.state.tab === 'active') {
				this.setState({ tasks });
			} else {
				this.setState({ expTasks: tasks });
			}
			/*
			if(this.state.tab === 'active') {
				this.getTasks();
			} else {
				this.getExpTasks();
			}
		*/});

	};

	changeTask = e => {

		console.log({
			id: e.id,
			title: e.title,
			desk: e.desk || '',
			time: e.time
		});

		this.api.Change({
			id: e.id,
			title: e.title,
			desk: e.desk || '',
			time: e.time
		}).then((res) => {

			if(res !== 'success'){
				this.openErrorSnackbar('Произошла ошибка. #10');
				return;
			}

			if(this.state.tab === 'active') {
				this.getTasks();
			} else {
				this.getExpTasks();
			}
		});

	};


	openModal = key => {
		const task = this.state.tasks[key] || this.state.expTasks[key];
		console.log(1, task)
		const { onCloseModal } = this;
		this.setState({
			modal: (
				<ModalRoot activeModal='task'>
					<ModalPage
						id='task'
						onClose={onCloseModal}
						header={
							<ModalPageHeader
								left={
									IS_PLATFORM_ANDROID ?
									<PanelHeaderButton onClick={onCloseModal}><Icon24Cancel /></PanelHeaderButton>
										:
										(
											<div style={{ display: 'flex'}}>
												<PanelHeaderButton onClick={() => this.openAlert(key, task.id)}>
													<Icon24Delete fill='#ccc'/>
												</PanelHeaderButton >
												<PanelHeaderButton
													onClick={() => this.setState({
														activeView: 'change',
														modal: null
												})}>
													<Icon24Write fill='#ccc'/>
												</PanelHeaderButton >
											</div>
										)
								}
								right={(
									<>
										{IS_PLATFORM_ANDROID ?
											(
												(
													<div style={{ display: 'flex'}}>
														<PanelHeaderButton onClick={() => this.openAlert(key, task.id)}>
															<Icon24Delete fill='#ccc'/>
														</PanelHeaderButton >
														<PanelHeaderButton
															onClick={() => this.setState({
																activeView: 'change',
																modal: null
															})}>
															<Icon24Write fill='#ccc'/>
														</PanelHeaderButton >
													</div>
												)
											)
										: <PanelHeaderButton onClick={onCloseModal}> <Icon24Dismiss /> </PanelHeaderButton> }
									</>
								)}
							>
								Информация
							</ModalPageHeader>
						}
					>
						<Group header={<Header mode="primary">{task && task.title}</Header>}>
							<List>
								{
									task.desk &&
									<Cell multiline>
										<InfoRow header="Описание">
											{task.desk}
										</InfoRow>
									</Cell>
								}
								<Cell multiline>
									{
										task.time &&
										<InfoRow header="Крайний срок выполнения">
											{moment(task.time, 'YYYY-MM-DD-hh-mm').fromNow()}
										</InfoRow>
									}
								</Cell>
							</List>
							<Div style={{ marginBottom: -10, marginTop: -5 }}>
								<Button
									size='xl'
									onClick={() => {
										onCloseModal(key);
										this.check(key, task.id);
									}}
									mode={task.done === 1? 'commerce' : 'primary'}
								>{task.done === 1 ? 'Задача выполнена' : 'Выполнить'}
								</Button>
							</Div>
							<Div/>
						</Group>
					</ModalPage>
				</ModalRoot>
			),
			curTask: task
			});
	};

	 render() {
		const {
			addTask, openModal, getTasks, getExpTasks, openDoneSnackbar, openErrorSnackbar, check, changeTask
		} = this;
	 	const setPState = this.setState.bind(this);
	 	const state = this.state;
	 	const { scheme, tasks, popout, modal, activeView, snackbar } = state;

		const props = {
			snackbar, setPState, state, tasks, scheme, addTask, openModal, getTasks, getExpTasks,
			openDoneSnackbar, openErrorSnackbar, check, changeTask
		};
		return (
		<ConfigProvider scheme={state.scheme}>
			<Root activeView={activeView}>
				<View id='home' activePanel='home' popout={popout} modal={modal} >
					<Home id='home' {...props}/>
				</View>
				<View id='add' popout={popout} activePanel='add'>
					<Add id='add' {...props} />
				</View>
				<View id='change' popout={popout} activePanel='change'>
					<Change id='change' task={state.curTask} {...props} />
				</View>
			</Root>
		</ConfigProvider>
	);
	}
};

export default App;

