import React, { Component } from 'react';

import Icon24Add from '@vkontakte/icons/dist/24/add';
import Icon56CheckCircleOutline from '@vkontakte/icons/dist/56/check_circle_outline';
import Icon56InfoOutline from '@vkontakte/icons/dist/56/info_outline';

import './add.css';
import {
	Separator, Div, Button, Panel, PanelHeader,
	Tabs, TabsItem, Spinner, Placeholder, Checkbox
} from "@vkontakte/vkui";



class Home extends Component {


	render() {

		const props = this.props;

		const { setPState, state, getTasks, getExpTasks } = props;

		const { tasks, expTasks, tab } = state;
		return (
			<Panel separator={false} id={props.id}>
				<PanelHeader separator={false}>Задачи</PanelHeader>
				<Tabs>
					<TabsItem
						onClick={ getTasks }
						selected={ tab === 'active' }
					>
						Активные
					</TabsItem>
					<TabsItem
						onClick={ getExpTasks }
						selected={ tab === 'expires' }
					>
						Завершенные
					</TabsItem>
				</Tabs>
				<Separator />
				<Div>

					{
						tasks.length > 0 && tab === 'active' ? ( tasks.map((e, key) => (
								<div key={key} >

									{ key !== 0 && <Separator/> }
										<div style={{ display: 'flex'}}>
											<div style={{ width: '12vw' }}>
												<Checkbox
													checked={tasks[key].done}
													onChange={() => props.check(key, tasks[key].id)}
												/>
											</div>
											<div
												className={`test ${props.state.scheme === 'bright_light' ? 'light' : 'dark'}`}
												onClick={() => props.openModal(key)}
											>
												<div className='test2'>
													{e.title.length < 30 ? e.title : `${e.title.split('').slice(0,30).join('')}...`}
												</div>
											</div>
										</div>
								</div>
							))

						) : tasks &&
							<Placeholder
								icon={<Icon56CheckCircleOutline />}
								action={
									<Button before={ <Icon24Add /> }
											size='l' mode='primary' onClick={() => {
												setPState({ activeView: 'add' });
											}}
									>
										Создать
									</Button>
								}

							>
								Активных задач нет
							</Placeholder>

					}
					{
						expTasks.length > 0 && tab === 'expires' ? ( expTasks.map((e, key) => (
								<div key={key} >

									{ key !== 0 && <Separator/> }

									<div style={{ display: 'flex'}}>
										<div style={{ width: '12vw' }}>
											<Checkbox
												checked={expTasks[key].done}
												onChange={() => props.check(key, expTasks[key].id)}
											/>
										</div>
										<div
											className={`test ${props.state.scheme === 'bright_light' ? 'light' : 'dark'}`}
											onClick={() => props.openModal(key)}
										>
											<div className='test2'>
												{e.title.length < 30 ? e.title : `${e.title.split('').slice(0,30).join('')}...`}
											</div>
										</div>
									</div>

								</div>
							))

						) : expTasks && tab === 'expires' && (
							<>
							<Placeholder
								icon={<Icon56InfoOutline />}
								action={<Button size="l" onClick={ getTasks } >Перейти к активным</Button>}
							>
								Завершённых задач нет
							</Placeholder>

							</>
						)
					}
					{
						((!tasks && tab === 'active') || ( !expTasks && tab === 'expires' )) && <Spinner/>
					}
					{
						tab === 'active' && tasks.length > 0 &&
						<Button before={ <Icon24Add /> }
								size='xl' mode='tertiary' onClick={() => {
							setPState({ activeView: 'add' });
							}}
						>
							Добавить
						</Button>
					}
				</Div>
				{props.snackbar}
			</Panel>
		);
	}

}

export default Home;
